
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import event from '../../../js/Event.js'
import { getYMDFormateTime, isArrayType, getImageUrl } from "../../../js/utility.js"
import { mapState } from 'vuex'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
name: 'tasksearch',
components: {
Flexbox,
FlexboxItem,
XTextarea,
Popup,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
computed: {
...mapState([
'crmSearchListInfo',
'taskList'
]),
PlateType() {
return PlateType;
},
},
data() {
return {
p: 1,
type: '',
searchText: '',
listItem: [],
addPopupShow: false, //
popupContent: '',
popupItem: null,
}
},
beforeDestroy() {
event.$off("searchtextDone");
},
beforeRouteLeave(to, from, next) {






if ((this.$route.params.type === 'project' && to.name === 'tasklist') || (this.$route.params.type === 'tasklist' && to.name === 'taskview') || (to.name === 'project' && to.params.action === 'select')) { //
if (to.name === 'project' && to.params.action === 'select') {
event.$on('selectedRelateItem', function(data) {
this.handleRelateInfo(data);
}.bind(this));
}
this.$store.commit('updateCrmSearchListInfo', {
type: this.$route.params.type, //type 
list: this.listItem,
p: this.p,
searchText: this.searchText,
scroll: this.$refs.my_scroller.getPosition().top,
loadingState: this.$refs.my_scroller.loadingState
});
} else { //
this.$store.commit('updateCrmSearchListInfo', {
type: '', //type 
list: [],
p: 1,
scroll: 0,
loadingState: 0
});

}
next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'project' && from.params.action === 'select') {
event.$off("selectedRelateItem");
}
next();
},
mounted() {
this.initInfo();
this.$refs.my_scroller.finishInfinite(true);
if (this.type === 'tasklist' && this.taskList.transfer) { //
this.taskList.transfer = false;
this.$store.commit('updateTaskList', this.taskList);
AjaxHttp('POST', 'm=task&a=order', { type_id: this.taskList.transferItem.id, task_id: this.taskList.currentItem.task_id }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.listItem.splice(wself.taskList.currentIndex, 1);
}
});
}
},
methods: {
initInfo() {
event.$on('searchtextDone', function(searchtext) {
this.searchContent(searchtext);
}.bind(this));
this.type = this.$route.params.type;
if (this.crmSearchListInfo.type === this.type) {
this.listItem = this.crmSearchListInfo.list;
this.p = this.crmSearchListInfo.p;
this.searchText = this.crmSearchListInfo.searchText;
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.crmSearchListInfo.scroll, false);
if (this.crmSearchListInfo.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}
},
searchContent(content) {
if (content === '' || this.searchText === content) { return; }
this.p = 1;
this.searchText = content;
this.listItem = [];
this.$refs.my_scroller.finishInfinite(false);
},
enterDetail(item) {
if (this.$route.params.action === 'default') {
if (this.type === 'project') {
this.$router.push({
name: 'tasklist',
params: {
id: item.id
}
});
} else if (this.type === 'tasklist') {
this.$router.push({
name: 'taskview',
params: {
id: item.task_id
}
});
}
} else if (this.$route.params.action === 'select') {
if (this.type === 'tasklist') {
this.taskList.currentItem = item;
this.$store.commit('updateTaskList', this.taskList);
}
event.$emit('selectedRelateItem', { type: this.type, item: item });
this.$router.go(-2);
}
},
refresh(done) {
this.p = 1;
this.getList(done);
},
infinite(done) {
this.getList(done);
},
getList(done) {
if (this.searchText === '') { done(true); return; }
var url = '';
var postParams = {};
if (this.type === 'project') {
url = 'm=task&a=typelist';
postParams['field'] = 'type_name';
} else if (this.type === 'tasklist') {
url = 'm=task&a=index';
postParams['field'] = 'subject';
postParams['type_id'] = this.$route.params.typeid;
}

postParams['p'] = this.p;
postParams['condition'] = 'contains';
postParams['search'] = this.searchText;


AjaxSilentHttp('POST', url, postParams, this, function(res, wself, success) {
if (success && isArrayType(res.list)) {
var newItems = [];
for (var i = 0; i < res.list.length; i++) {
newItems[i] = wself.getNewItem(res.list[i]);
}
if (wself.p === 1) {
wself.listItem = newItems;
} else {
wself.listItem = wself.listItem.concat(newItems);
}
if (typeof done === 'function') {
if (wself.p === 1 && res.list.length < 10) {
done(true);
} else if (res.list.length === 0) {
done(true);
} else {
done();
}
}
wself.p = wself.p + 1;
}
});
},
getNewItem(item) {
if (this.type === 'project') {
item.create_time = ' ' + getYMDFormateTime(item.create_time);
item.count = item.done_count + '/' + item.sum_count;
item.percent = parseInt(parseInt(item.done_count) * 100 / parseInt(item.sum_count));
return item;
}
return item;
},
getTimeContent(item) {
return getYMDFormateTime(item.create_date);
},
getImgeSrc(item) {
return getImageUrl(item.thumb_path);
},
priorityColor(item) {
if (item.priority === '') {
return '#f96868';
} else if (item.priority === '') {
return '#46be8a';
} else if (item.priority === '') {
return '#f2a654';
}
return '#FFF';
},
getStatusMark(item) {
if (item.status === '') {
return require('../../../assets/selected.png');
}
return require('../../../assets/un_selected.png');
},
isCompleteStatus(item) {
if (item.status === '') {
return true;
}
return false;
},
changeSatus(item, index) {
AjaxHttp('POST', 'm=task&a=edit', { id: item.task_id, status: item.status === '' ? '' : '' }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.$refs.my_scroller.triggerPullToRefresh();
}
});
},
popupSave() { //
if (this.popupContent === '') {
this.$vux.toast.text('', 'top');
}
AjaxHttp('POST', 'm=task&a=edittypeajax', { name: this.popupContent, type_id: this.popupItem.id }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.$refs.my_scroller.triggerPullToRefresh();
wself.addPopupShow = false;
}
});
},
editProjectName(item) {
this.popupItem = item;
this.popupContent = item.name;
this.addPopupShow = true;
},
onSwipeoutClick(item, type, index) {
this.taskList.currentItem = item;
this.taskList.currentIndex = index;
this.$store.commit('updateTaskList', this.taskList);
if (type === 'transfer') {
this.$router.push({
name: 'project',
params: {
action: 'select'
}
});
}
},
handleRelateInfo(data) {
if (data.type === 'project') {
this.taskList.transfer = true;
this.taskList.transferItem = data.item;
this.$store.commit('updateTaskList', this.taskList);
}
}
}
}




